.box {
    position: relative;
    max-width: 520px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: rgba(0, 0, 0, 0.5);
    overflow: hidden;
    border-radius: 20px;

    .container {
        color: #fff;
        z-index: 1;
        padding: 25px;
        width: 100%;
        height: 100%;

        ul {
            display: flex;
            flex-wrap: wrap;
            justify-content: center;
            li {
                display: flex;

                button {
                    all: unset;
                    cursor: pointer;
                    width: 200px;
                    height: 100px;
                    text-align: center;
                    // padding: 40px;
                    margin: 10px 15px;
                    position: relative;
                    display: block;
                    text-decoration: none;
                    overflow: hidden;
                    border-radius: 5px;
                    &:hover {
                        .wave {
                            top: -50px;
                        }
                    }

                    span {
                        position: relative;
                        z-index: 1;
                        color: white;
                        letter-spacing: 1px;
                        font-family: cursive;
                    }

                    .wave {
                        width: 200px;
                        height: 200px;
                        background-color: #3e486c;
                        box-shadow: inset 0 0 50px rgba(0, 0, 0, 0.5);
                        position: absolute;
                        left: 0;
                        top: -80px;
                        transition: 0.4s;

                        &::before,
                        &::after {
                            width: 200%;
                            height: 200%;
                            content: "";
                            position: absolute;
                            top: 0;
                            left: 50%;
                            transform: translate(-50%, -75%);
                        }

                        &::before {
                            border-radius: 45%;
                            background-color: rgba(20, 20, 20, 1);
                            animation: wave 5s linear infinite;
                        }

                        &::after {
                            border-radius: 40%;
                            background-color: rgba(20, 20, 20, 0.5);
                            animation: wave 10s linear infinite;
                        }
                    }
                }
            }
        }
    }

    &::before {
        content: "";
        position: absolute;
        width: 150px;
        //   width: 100%;
        height: 140%;
        background: linear-gradient(#00ccff, #d400d4);
        animation: animate 4s linear infinite;
    }

    &::after {
        content: "";
        position: absolute;
        inset: 4px;
        background: #0e1538;
        border-radius: 16px;
    }
}
